import { React } from 'react';
import { Accordion, Alert, Dropdown } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux';
import * as event from './datastoreaction';

export default function List() {

    const action = useDispatch();
    window.onload = () => { 
        (() => {
            event.readline(action);
            event.readsteps(action);
        })()       
    }

    const data = useSelector(data => data.line);
    const steps = useSelector(data => data.step);

    console.log("aany atrandart");

    //return fun of now and pfun
    //map arr of keys

return (
    <div>
        { Object.keys(data).length != 0 && Object.keys(data).map((id) =>
        <Accordion defaultActiveKey="5" key={ id } style={ { textDecoration: (data[id].iscrossedout && "line-through") } } onClick={ (e) => { e.stopPropagation(); } }>
            <Accordion.Item eventKey="0">
                <Accordion.Header onClick={ (e) => {
                    e.stopPropagation();
                    action(event.editmode(data[id], id, "edit"));//pinx//id
                    //render 
                    //props.updatedatabase(id);
                } } onDoubleClick={ (e) => {
                    e.stopPropagation();
                    action(event.addmode("add"));
                    //render 
                    //props.updatedatabase(id+"9");
                } } >
                    <Alert variant={ data[id].color } >
                        <Alert.Heading>{ data[id].title }</Alert.Heading>
                        <p>
                            { data[id].description }
                        </p>
                        <p>
                            { data[id].links.map((val, inx) => {//use link or a for link to open link in new tab
                                return <Alert.Link key={ inx } href={ val }>
                                    { val }
                                    <br />
                                </Alert.Link>
                            
                            }) }
                        </p>
                        <hr />
                        <p className="mb-0">
                            { data[id].hint }
                        </p>
                        <Dropdown onClick={ (e) => { e.stopPropagation(); } }>
                            <Dropdown.Toggle id="dropdown-basic" >
                                ...
                            </Dropdown.Toggle>
                            <Dropdown.Menu>
                                <Dropdown.Item href="" key="1" onClick={ (e) => {
                                    e.stopPropagation();
                                    action(event.addstepsmode("addsteps", id));//pinx//id
                                    // action(event.addstep(inx, id));
                                    //render 
                                    //props.updatedatabase(id+"445");
                                } }>add steps</Dropdown.Item>
                                <Dropdown.Item href="" key="2" onClick={ (e) => {
                                    e.stopPropagation();
                                    data[id].iscrossedout == false ?
                                        event.crossoutline(action, id, true) ://pinx//id
                                        event.crossoutline(action, id, false);
                                } }>cross out</Dropdown.Item>
                                <Dropdown.Item href="" key="3" onClick={ (e) => {
                                    e.stopPropagation();
                                    const stepsarr=data[id].steps;
                                    event.removeline(action, id).then(()=>{
                                        stepsarr.map((cid)=>event.removestep(action,cid))
                                    })//pinx//id
                                    action(event.addmode("add"))
                                } }>remove</Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </Alert>
                </Accordion.Header>
                <Accordion.Body>
                    { (data[id].hasOwnProperty("steps") && Object.keys(steps).length != 0) && data[id].steps.map((sval, inx) => {
                        return <Alert variant={ steps[sval].color } key={ sval } onClick={ (e) => {
                            e.stopPropagation();
                            action(event.editstepsmode(steps[sval], sval, "editstep")); console.log(sval);
                        } } onDoubleClick={ (e) => {
                            e.stopPropagation();
                            action(event.addstepsmode("addsteps"));
                        } } style={ { textDecoration: (steps[sval].iscrossedout && "line-through") } }>
                            { steps[sval].text }
                            <Dropdown>
                                <Dropdown.Toggle id="dropdown-basic">
                                    ...
                                </Dropdown.Toggle>
                                <Dropdown.Menu>
                                    <Dropdown.Item href="" key="1" onClick={ (e) => {
                                        e.stopPropagation();
                                        action(event.editstepsmode(sval, steps[sval].id, "editstep"));
                                        steps[sval].iscrossedout == false ?
                                            event.crossoutstep(action, sval, true) :
                                            event.crossoutstep(action, sval, false);
                                    } }>cross out</Dropdown.Item>
                                    <Dropdown.Item href="" key="2" onClick={ (e) => {
                                        e.stopPropagation();
                                        event.outstep(action, id,sval).then(() => { 
                                            event.removestep(action, sval);
                                        });//pinx//id
                                        action(event.addstepsmode("addsteps"));
                                        //render 
                                        //props.updatedatabase(steps[sval]+"oo");
                                    } }>remove</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </Alert>
                    }) || <span style={ {color:"white"} }>- NO STEPS AVAILABLE </span> }
                </Accordion.Body>
            </Accordion.Item>
        </Accordion>
        ) }
    </div>
  )
}