const mode = (data = {}, action) => { 

    switch (action.type) { 
        case 'editmode':
            sysstates.index = action.id;
            sysstates.state = action.state;
            sysstates.attachments = action.data;
            data = { ...sysstates };
            console.log(sysstates);
            return data;
        case 'addmode':
            sysstates.state = action.state;
            data = { ...sysstates };
            console.log(sysstates);
            return data;
        case 'addstepsmode':
            sysstates.index = action.parentid;
            sysstates.state = action.state;
            data = { ...sysstates };
            console.log(sysstates);
            return sysstates;
        case 'editstepsmode':
            sysstates.index = action.id;
            sysstates.state = action.state;
            sysstates.attachments = action.data
            data = { ...sysstates };
            console.log(sysstates);
            return data;
        default:
        break;
    }
    return data;
}

const reducers = combineReducers({
    step: todostep,
    line: todoline,
    mode: mode
})

export const todo = createStore(reducers, applyMiddleware(reduxthunk));



