// Initialize Firebase
const app = initializeApp(firebaseConfig);

const db = getDatabase(app);

export const readline = async(action) => { 
    
    await get(child(ref(db), `line`)).then((snapshot) => {
    if (snapshot.exists()) {
        console.log(snapshot.val());
        action({
            type: 'readline',
            data: snapshot.val()
        })
    } else {
        console.log("No data available");
    }
    }).catch((error) => {
    console.error(error);
    });
    
}

export const readsteps = async(action) => { 

    await get(child(ref(db), `step`)).then((snapshot) => {
    if (snapshot.exists()) {
        console.log(snapshot.val());
        action({
            type: 'readsteps',
            data: snapshot.val()
        })
    } else {
        console.log("No data available");
    }
    }).catch((error) => {
    console.error(error);
    });

}

export const addline = async (action, id, data) => {
    
    await set(ref(db, 'line/' + id), data).then(() => {
        action({
            type: "addline",
            id: id,
            data: data
        })      
    });

}
export const removeline = async (action, id) => {

    await remove(ref(db, "line/" + id)).then(() => { 
        action({
            type: "removeline",
            id:id         
        })        
    })

}
export const crossoutline = async(action, id, state) => { 
    const updates = {};
    updates['line/' + id + '/iscrossedout/'] =  state;
    await update(ref(db), updates).then(() => { 
        action({
            type: "crossoutline",
            id:id,
            state:state
        })
    });
}
export const editline = async (action, id, data) => { 
    data.isedited = true;
    const updates = {};
    updates['line/' + id + '/'] =  data;
    await update(ref(db), updates).then(() => {
        action({
            type: "editline",
            id: id,
            data: data
        })
    });
}
//todo get steps arr from existing not server
export const rigststep = async(action,id, pid) => { 

    await get(child(ref(db), "line/"+pid+"/steps")).then(async (snapshot) => {
    if (snapshot.exists()) {
        console.log(snapshot.val());
        let stepsarr = snapshot.val();
        stepsarr.push(id);
        const updates = {};
        updates['line/' + pid + '/steps/'] =  stepsarr;
        await update(ref(db), updates ).then(() => { 
            action({
                type: "rigststep",
                id:id,
                pid: pid
            })
        });
        
    } else {
        await set(ref(db, 'line/' + pid + "/steps"), [id]).then(() => { 
            action({
                type: "rigststep",
                id:id,
                pid: pid
            })
        });
        }
    }).catch((error) => {
        console.error(error);
    });
}
//todo get steps arr from existing not server
export const outstep = async(action, pid, id) => {
    await get(child(ref(db), "line/"+pid+"/steps")).then(async (snapshot) => {
    if (snapshot.exists()) {
        console.log(snapshot.val()); 
        const stepsarr = snapshot.val().filter((v) => v != id);
        const updates = {};
        updates['line/' + pid + '/steps/'] = stepsarr;
        await update(ref(db), updates ).then(() => { 
            action({
                type: "outstep",
                pid: pid,
                id:id
            })
        });
        
    } else {
            console.log("No data available");
        }
    }).catch((error) => {
        console.error(error);
    });
}

