export const editmode = (line,id,state) => { 
    return {
        type: "editmode",
        id: id,
        data: line,
        state:state
    }
}
export const addmode = (state) => { 
    return {
        type: "addmode",
        state:state
    }
}
export const addstepsmode = (state,pid) => { 
    return {
        type: "addstepsmode",
        state: state,
        parentid:pid
    }
}
export const editstepsmode = (step,id,state) => { 
    return {
        type: "editstepsmode",
        id: id,
        data: step,
        state:state
    }
}