import React, { useRef } from 'react'
import { Button, Form, Dropdown } from 'react-bootstrap';
import { useSelector,useDispatch } from 'react-redux';
import { addline, addstep, editline, editstep, addmode,rigststep } from './datastoreaction';
import getcode from './codegnr';

export default function Controls() {

    const excolor = useRef();
    const todotitle = useRef();
    const tododescription = useRef();
    const todohint = useRef();
    const todolinks = useRef();
    const todostep = useRef();
    const btn = useRef();


    let todocolor = "primary";
    let btnprocess = addL;
    let controlsmode = "line";


    const mode = useSelector(d => d.mode);

    const action = useDispatch();

    const date = new Date();

    
    if (mode.state == "edit") {

        todotitle.current.value = mode.attachments.title;
        tododescription.current.value = mode.attachments.description;
        todohint.current.value = mode.attachments.hint;
        todolinks.current.value = "";
        for (let link of mode.attachments.links)
            todolinks.current.value += link + "\n";
        todolinks.current.value = todolinks.current.value.trim()
        excolor.current.classList.replace(excolor.current.classList.item(0), "bg-" + mode.attachments.color);
        todocolor = mode.attachments.color;
        btn.current.innerText = "Edit";
        
        controlsmode = "line";
        btnprocess = editL;
        
    } else if (mode.state == "add") {
        
        todotitle.current.value = "";
        tododescription.current.value = "";
        todohint.current.value = "";
        todolinks.current.value = "";
        excolor.current.classList.replace(excolor.current.classList.item(0), "bg-primary");
        todocolor = "primary";
        btn.current.innerText = "Add";

        controlsmode = "line";
        btnprocess = addL;

    } else if (mode.state == "addsteps") {

        todostep.current.value = "";
        excolor.current.classList.replace(excolor.current.classList.item(0), "bg-primary");
        todocolor = "primary";
        btn.current.innerText = "Add step";

        controlsmode = "steps";
        btnprocess = addS;

    } else if (mode.state == "editstep") { 

        todostep.current.value = mode.attachments.text;
        excolor.current.classList.replace(excolor.current.classList.item(0), "bg-" + mode.attachments.color);
        todocolor = mode.attachments.color;
        btn.current.innerText = "Edit step";

        controlsmode = "steps";
        btnprocess = editS;
    }

    function colorpeeker(c) { 
        excolor.current.classList.replace(excolor.current.classList.item(0), c);
        todocolor = c.split("-")[1];
    }

    function editL() {
        mode.attachments.title = todotitle.current.value;
        mode.attachments.description = tododescription.current.value;
        const linksarray=[...todolinks.current.value.split("\n")];
        mode.attachments.links = linksarray;
        mode.attachments.hint = todohint.current.value;
        mode.attachments.color = todocolor;
        
        //dispatch addline
        editline(action,mode.index, mode.attachments);
        
        //render
        //props.updatedatabase(mode.attachments);
    }

    function addL()
    {
        //--generate id
        const id = getcode(true, true, true, true, false, 5, "string");
        //todo check id not exist

        //--get data
        let tododata = {}
        tododata.id = id;
        tododata.title = todotitle.current.value;
        tododata.description = tododescription.current.value;
        const linksarray=todolinks.current.value.split("\n");
        tododata.links = linksarray;//.length != 0 ? linksarray : null;
        tododata.hint = todohint.current.value;
        tododata.color =  todocolor;
        tododata.steps = [];
        tododata.createdat = date.getHours() + ":" + date.getMinutes() + ":" +
            date.getSeconds() + " _ " +
            (date.getMonth() + 1) + "/" +date.getDate() + "/" +date.getFullYear();
        tododata.date = date.getDate();
        tododata.time = date.getTime();
        tododata.isedited = false;
        tododata.iscrossedout = false;
        tododata.clintinfo = navigator.userAgent;

        //dispatch addline
        addline(action,id, tododata);
        
        //render
        //props.updatedatabase(tododata);
    }

    function addS() { 
        console.log("ssd");
        //--generate id
        const id = getcode(true, true, true, true, false, 5, "string");
        //todo check id not exist
        //--get data
        let step = {};
        step.id = id;
        step.text = todostep.current.value;
        step.color = todocolor;
        step.createdat = date.getHours() + ":" + date.getMinutes() + ":" +
            date.getSeconds() + " _ " +
            (date.getMonth() + 1) + "/" +date.getDate() + "/" +date.getFullYear();
        step.date = date.getDate();
        step.time = date.getTime();
        step.isedited = false;
        step.iscrossedout = false;
        step.clintinfo = navigator.userAgent;

        //dispatch addline
        addstep(action, id, step).then(() => rigststep(action, id, mode.index));
        
        //render
        //props.updatedatabase(step);
    }

    function editS() { 
        console.log("ssde");
        mode.attachments.text = todostep.current.value;
        mode.attachments.color = todocolor;
        
        //dispatch addline
        editstep(action,mode.index, mode.attachments);
        
        //render
        //props.updatedatabase(mode.attachments);
    }

  return (
    <div className='controls'>
        <Form className='row'>
            <div className={"col-9 sec1 "+(controlsmode!='line'&&"d-none")} >
                <Form.Label htmlFor='todotitle'>To do title :</Form.Label>
                <Form.Control type='text' id='todotitle' ref={todotitle}/>
                <Form.Label htmlFor='tododescription'>To do description :</Form.Label>
                <Form.Control as="textarea" id='tododescription' ref={tododescription}/>
                <Form.Label htmlFor='todohint'>To do hint :</Form.Label>
                <Form.Control type='text' id='todohint' ref={todohint}/>
                <Form.Label htmlFor='todolinks'>To do links :</Form.Label>
                <Form.Control as="textarea" id='todolinks' rows={3} ref={todolinks}/>
            </div>
            <div className={"col-9 sec1 "+(controlsmode!='steps'&&"d-none")}>
                <Form.Label htmlFor='todostep'>To do step :</Form.Label>
                <Form.Control type='text' id='todostep' ref={todostep}/>
            </div>
            <div className='col-3 sec2'>
                <Dropdown>
                    <Dropdown.Toggle variant="light" id="dropdown-basic"  className='colorpeek'>
                        Check color {" "}
                        <span ref={excolor} className='bg-primary p-1 dropdownex'></span>
                    </Dropdown.Toggle>
                    <Dropdown.Menu>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-primary")}}>Primary <div className='bg-primary p-1'></div></Dropdown.Item>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-secondary")}}>Secondary <div className='bg-secondary p-1'></div></Dropdown.Item>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-success")}}>Success <div className='bg-success p-1'></div></Dropdown.Item>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-danger")}}>Danger <div className='bg-danger p-1'></div></Dropdown.Item>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-warning")}}>Warning <div className='bg-warning p-1'></div></Dropdown.Item>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-info")}}>Info <div className='bg-info p-1'></div></Dropdown.Item>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-light")}}>Light <div className='bg-light p-1'></div></Dropdown.Item>
                        <Dropdown.Item href="" onClick={()=>{colorpeeker("bg-dark")}}>Dark <div className='bg-dark p-1'></div></Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                  { controlsmode !='line'&&<Button className='w-50 mb-2 addbutton bg-danger ' onClick={() => { 
                        action(addmode("add"));
                        //render 
                        //props.updatedatabase("9");
                  }}>Cancel</Button> }
                  <Button ref={btn} className='w-50 addbutton' onClick={() => { btnprocess() }}>Add</Button>
            </div>

        </Form>
    </div>
  )
}
